import React, { Component } from 'react';
import './PageNotFound.css';

class PageNotFound extends Component {
  render() {
    return (
      <div className="PageContent">
        <h1>This page doesn't exist :(</h1>
      </div>
    );
  }
}

export default PageNotFound;



